import React, { Component, PropTypes } from 'react'
import Sortable from 'sortablejs';

class PageEditor extends Component {

    constructor(props) {
        super(props);
    }

    componentDidMount() {
        var el = document.getElementById('to');
        var sortable = Sortable.create(el,
        {
            group: {
                name: "shared",
                pull: "clone",
                revertClone: true
            },
            sort: true
        });
        var el1 = document.getElementById('from');
        var sortable1 = Sortable.create(el1,
        {
            group: "shared",
            sort: false
        });
    }

    render() {
        return <div>
            <div style={{
                width: '50vw',
                float: 'left'
            }}>
                <ul id="to">
                    <li>item 1</li>
                    <li>item 2</li>
                    <li>item 3</li>
                </ul>
            </div>
            <div style={{
                width: '50vw',
                float: 'right'
            }}>
                <ul id="from">
                    <li>item 1</li>
                    <li>item 2</li>
                    <li>item 3</li>
                </ul>
            </div>
        </div>;
    }
}

export default PageEditor;


